/****************************************************************************/
/* NUMBERS */
/****************************************************************************/

.c-number {
	font-style: normal;
	font-weight: normal;
	line-height: 1;
}

.c-number_md {
	font-size: 28px;
	letter-spacing: -.8px;
}

.c-number_lg {
	font-size: 36px;
	letter-spacing: -1px;
}

.c-number_xl {
	font-size: 44px;
	letter-spacing: -1.2px;
}

.c-number_xxl {
	font-size: 56px;
	letter-spacing: -2px;
}
