/****************************************************************************/
/* TITLES */
/****************************************************************************/

.c-body {
	font-style: normal;
	font-weight: normal;
}

.c-body_xxs {
	font-size: 11px;
	line-height: 14px;
	letter-spacing: -.1px;
}

.c-body_xs {
	font-size: 11px;
	line-height: 14px;
	letter-spacing: -.3px;
}

.c-body_sm {
	font-size: 13px;
	line-height: 16px;
	letter-spacing: -.3px;
}

.c-body_md {
	font-size: 15px;
	line-height: 18px;
	letter-spacing: -.3px;
}

.c-body_lg {
	font-size: 17px;
	line-height: 20px;
	letter-spacing: -.3px;
}

.c-body_xl {
	font-size: 19px;
	line-height: 24px;
	letter-spacing: -.5px;
}
