/* ACTIONS */
/****************************************************************************/
.c-action {
  font-style: normal;
  font-weight: bold;
}

.c-action_sm {
  font-size: 13px;
  line-height: 16px;
  letter-spacing: -0.1px;
}

.c-action_md {
  font-size: 15px;
  line-height: 18px;
  letter-spacing: -0.1px;
}

.c-action_lg {
  font-size: 17px;
  line-height: 20px;
  letter-spacing: -0.3px;
}

.c-action_xl {
  font-size: 19px;
  line-height: 24px;
  letter-spacing: -0.5px;
}

.c-action_xxl {
  font-size: 21px;
  line-height: 28px;
  letter-spacing: -0.5px;
}

/****************************************************************************/
/* TITLES */
/****************************************************************************/
.c-body {
  font-style: normal;
  font-weight: normal;
}

.c-body_xxs {
  font-size: 11px;
  line-height: 14px;
  letter-spacing: -.1px;
}

.c-body_xs {
  font-size: 11px;
  line-height: 14px;
  letter-spacing: -.3px;
}

.c-body_sm {
  font-size: 13px;
  line-height: 16px;
  letter-spacing: -.3px;
}

.c-body_md {
  font-size: 15px;
  line-height: 18px;
  letter-spacing: -.3px;
}

.c-body_lg {
  font-size: 17px;
  line-height: 20px;
  letter-spacing: -.3px;
}

.c-body_xl {
  font-size: 19px;
  line-height: 24px;
  letter-spacing: -.5px;
}

/****************************************************************************/
/* TITLES */
/****************************************************************************/
.c-title {
  font-weight: bold;
  font-style: normal;
  position: relative;
}

.c-title_sm {
  font-size: 14px;
  line-height: 16px;
  letter-spacing: -.1px;
}

.c-title_md {
  font-size: 16px;
  line-height: 18px;
  letter-spacing: -.3px;
}

.c-title_lg {
  font-size: 18px;
  line-height: 20px;
  letter-spacing: -.5px;
}

.c-title_xl {
  font-size: 24px;
  line-height: 28px;
  letter-spacing: -.8px;
}

.c-title_xxl {
  font-size: 28px;
  line-height: 32px;
  letter-spacing: -1px;
}

/****************************************************************************/
/* SUBTITLES */
/****************************************************************************/
.c-subtitle {
  font-weight: 600;
}

.c-subtitle_xs {
  font-size: 12px;
  line-height: 14px;
  letter-spacing: -0.3px;
}

.c-subtitle_lg {
  font-size: 19px;
  line-height: 22px;
  letter-spacing: -0.5px;
}

/****************************************************************************/
/* CAPTIONS */
/****************************************************************************/
.c-caption {
  text-transform: uppercase;
}

.c-caption_xs {
  font-size: 10px;
  line-height: 10px;
  letter-spacing: -0.1px;
}

.c-caption_sm {
  font-weight: 500;
  font-size: 12px;
  line-height: 12px;
  letter-spacing: -0.1px;
}

/****************************************************************************/
/* NUMBERS */
/****************************************************************************/
.c-number {
  font-style: normal;
  font-weight: normal;
  line-height: 1;
}

.c-number_md {
  font-size: 28px;
  letter-spacing: -.8px;
}

.c-number_lg {
  font-size: 36px;
  letter-spacing: -1px;
}

.c-number_xl {
  font-size: 44px;
  letter-spacing: -1.2px;
}

.c-number_xxl {
  font-size: 56px;
  letter-spacing: -2px;
}
