/****************************************************************************/
/* TITLES */
/****************************************************************************/

.c-title {
	font-weight: bold;
	font-style: normal;
	// color: #000;
	position: relative;
}

.c-title_sm {
	font-size: 14px;
	line-height: 16px;
	letter-spacing: -.1px;
}

.c-title_md {
	font-size: 16px;
	line-height: 18px;
	letter-spacing: -.3px;
}

.c-title_lg {
	font-size: 18px;
	line-height: 20px;
	letter-spacing: -.5px;
}

.c-title_xl {
	font-size: 24px;
	line-height: 28px;
	letter-spacing: -.8px;
}

.c-title_xxl {
	font-size: 28px;
	line-height: 32px;
	letter-spacing: -1px;
}
